import React, { Component } from "react";
import { AiOutlineLoading3Quarters } from "react-icons/ai";
import {
  GetTrainingsListByStatus,
  TrainingByImplementationStatusDetails,
  TrainingImplementationStatus,
} from "../../actions/training-offers";
import Alert from "../../components/Alert/Alert";
import { DATE, search } from "../../utils/functions";

interface TrainingsListProps {
  onSelected: (data: TrainingByImplementationStatusDetails) => void;
}
interface TrainingsListState {
  loading: boolean;
  error: string;
  trainings: TrainingByImplementationStatusDetails[] | null;
  selectedStatus: TrainingImplementationStatus;
  searchData: string;
}

export class TrainingsList extends Component<
  TrainingsListProps,
  TrainingsListState
> {
  constructor(props: TrainingsListProps) {
    super(props);

    this.state = {
      loading: false,
      error: "",
      trainings: null,
      selectedStatus: TrainingImplementationStatus.INPROGRESS,
      searchData: "",
    };
  }
  LoadTrainings = (selectedStatus: TrainingImplementationStatus) => {
    this.setState({ loading: true, error: "", selectedStatus: selectedStatus });
    GetTrainingsListByStatus(
      selectedStatus,
      (
        loading: boolean,
        res: {
          type: "success" | "error";
          msg: string;
          data: TrainingByImplementationStatusDetails[];
        } | null
      ) => {
        this.setState({ loading: loading });
        if (res?.type === "success") {
          this.setState({ trainings: res.data, error: "", loading: false });
        }
        if (res?.type === "error") {
          this.setState({ trainings: [], error: res.msg, loading: false });
        }
      }
    );
  };
  FilteredTrainings = () => {
    if (this.state.trainings === null) {
      return [];
    }
    var response = this.state.trainings;
    return search(
      response,
      this.state.searchData
    ) as TrainingByImplementationStatusDetails[];
  };
  componentDidMount(): void {
    this.LoadTrainings(this.state.selectedStatus);
  }
  render() {
    const trainingStatuses: {
      title: string;
      key: TrainingImplementationStatus;
    }[] = [
      {
        title: "In progress",
        key: TrainingImplementationStatus.INPROGRESS,
      },
      {
        title: "Completed",
        key: TrainingImplementationStatus.COMPLETED,
      },
      {
        title: "Canceled",
        key: TrainingImplementationStatus.CANCELLED,
      },
      {
        title: "Ready for evaluation",
        key: TrainingImplementationStatus.READY_FOR_EVALUATION,
      },
      {
        title: "Draft",
        key: TrainingImplementationStatus.DRAFT,
      },
    ];
    if (this.state.loading === true || this.state.trainings === null) {
      return (
        <div>
          <div className="bg-gray-100 rounded-md w-full flex flex-col items-center justify-center p-3 py-6">
            <div>
              <AiOutlineLoading3Quarters
                className={`loading-animate text-6xl text-accent-900 dark:text-white`}
              />
            </div>
            <div className="text-xl font-light animate__animated animate__fadeIn animate__infinite">
              Loading, please wait...
            </div>
          </div>
        </div>
      );
    }
    return (
      <div>
        <div>
          <div className="flex flex-row items-center gap-3 w-full mb-6">
            <select
              onChange={(e) =>
                this.LoadTrainings(
                  e.target.value as TrainingImplementationStatus
                )
              }
              className="px-3 py-2 rounded border border-primary-500 bg-primary-100 font-bold text-primary-700 w-1/3"
            >
              {trainingStatuses.map((stat, s) => (
                <option key={s + 1} value={stat.key}>
                  {stat.title}
                </option>
              ))}
            </select>
            <input
              type="search"
              className="px-3 py-2 rounded border border-gray-400 w-2/3"
              placeholder="Search by name"
              value={this.state.searchData}
              onChange={(e) =>
                this.setState({ searchData: e.target.value, error: "" })
              }
            />
          </div>
          {this.state.error !== "" && (
            <div className="my-3">
              <Alert
                theme="danger"
                onClick={() => this.setState({ error: "" })}
              >
                {this.state.error}
              </Alert>
            </div>
          )}
          {this.state.trainings.length === 0 ? (
            <div className="text-center p-4 rounded-md bg-gray-100 w-full">
              <div className="text-2xl font-bold">No trainings found!</div>
              <div className="text-gray-500">
                For selected status, there are no trainings found
              </div>
            </div>
          ) : (
            <div className="w-full overflow-x-auto">
              <table className="min-w-full text-left text-sm">
                <thead>
                  <tr>
                    <th className="px-3 py-2 border">#</th>
                    <th className="px-3 py-2 border">Training title</th>
                    <th className="px-3 py-2 border">Topic</th>
                    <th className="px-3 py-2 border">Start date</th>
                    <th className="px-3 py-2 border">Ending date</th>
                    <th className="px-3 py-2 border">Training provider</th>
                    <th className="px-3 py-2 border">Phone number</th>
                    <th className="px-3 py-2 border"></th>
                  </tr>
                </thead>
                <tbody>
                  {this.FilteredTrainings().length === 0 ? (
                    <tr>
                      <td className="text-xl text-center font-light">
                        No result found!
                      </td>
                    </tr>
                  ) : (
                    this.FilteredTrainings().map((item, i) => (
                      <tr
                        key={i + 1}
                        className="cursor-pointer hover:bg-accent-400 hover:text-accent-900 group"
                        onClick={() => this.props.onSelected(item)}
                      >
                        <td className="px-3 py-2 border">{i + 1}</td>
                        <td className="px-3 py-2 border">{item.title}</td>
                        <td className="px-3 py-2 border">{item.topic}</td>
                        <td className="px-3 py-2 border">
                          {DATE(item.start_date)}
                        </td>
                        <td className="px-3 py-2 border">
                          {DATE(item.end_date)}
                        </td>
                        <td className="px-3 py-2 border">
                          {item.development_partner_name}
                        </td>
                        <td className="px-3 py-2 border">{item.dp_contact}</td>
                        <td className="px-1 py-1 border w-10">
                          <div className="bg-gray-100 group-hover:bg-accent-900 group-hover:text-white font-bold px-3 py-2 rounded w-max text-current cursor-pointer">
                            Select
                          </div>
                        </td>
                      </tr>
                    ))
                  )}
                </tbody>
              </table>
            </div>
          )}
        </div>
      </div>
    );
  }
}
