import React, { Component } from "react";
import { CenterItemInterface } from "../../actions/training_settings_create_edit";
import { DistrictTypes } from "../../types/TransferTypes";

interface CentersListProps {
  centers: CenterItemInterface[];
  GetFilteredData: () => CenterItemInterface[];
  districts: DistrictTypes[];
}
interface CentersListState {}

export class CentersList extends Component<CentersListProps, CentersListState> {
  render() {
    return (
      <div className="w-full overflow-x-auto">
        <table className="w-full text-sm text-left">
          <thead className="bg-primary-100 text-primary-800 border-4 border-white">
            <tr>
              <th className="px-3 py-2">#</th>
              <th className="px-3 py-2">District</th>
              <th className="px-3 py-2">Center name</th>
              <th className="px-3 py-2">Center type</th>
              <th className="px-3 py-2"></th>
            </tr>
          </thead>
          <tbody>
            {this.props.centers === null ? (
              <tr>
                <td
                  colSpan={7}
                  className="text-xl px-4 py-3 font-light text-center"
                >
                  Loading, please wait...
                </td>
              </tr>
            ) : this.props.GetFilteredData().length === 0 ? (
              <tr>
                <td
                  colSpan={7}
                  className="text-xl px-4 py-3 font-light text-center"
                >
                  No data found!
                </td>
              </tr>
            ) : (
              this.props.GetFilteredData().map((item, i) => (
                <tr
                  key={i + 1}
                  className={`${
                    i % 2 !== 0 ? "bg-gray-100" : "bg-white"
                  } hover:bg-accent-400 hover:text-accent-900 cursor-pointer`}
                  //   onClick={() => this.setState({ preview: item })}
                >
                  <td className="px-3 py-2">{i + 1}</td>
                  <td className="px-3 py-2">{item.district_name}</td>
                  <td className="px-3 py-2">{item.center_name}</td>
                  <td className="px-3 py-2">{item.center_type_name}</td>
                  <td className="px-2 py-2">
                    <div
                      className={`${
                        item.status === "ACTIVE"
                          ? "bg-gray-100 text-gray-700"
                          : "bg-red-100 text-red-700"
                      } px-1 py-0 w-max rounded-md text-xs`}
                    >
                      {item.status}
                    </div>
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      </div>
    );
  }
}
