import React, { Component } from "react";
import { AiOutlineLoading3Quarters } from "react-icons/ai";
import { IoIosArrowDown } from "react-icons/io";
import { MdClose } from "react-icons/md";
import {
  CenterTypeDetailsInterface,
  CreateNewCenter,
} from "../../actions/training_settings_create_edit";
import Button from "../../components/Buttons/Button";
import TextInput from "../../components/Inputs/TextInput";
import SelectCustom from "../../components/SelectCustom/SelectCustom";
import { DistrictTypes } from "../../types/TransferTypes";

interface AddCenterProps {
  districts: DistrictTypes[];
  centerTypes: CenterTypeDetailsInterface;
  onCreated: () => void;
}
interface AddCenterState {
  name: string;
  district_id: string;
  center_type_id: string;
  loading: boolean;
  success: string;
  error: {
    target: "main" | "name" | "district_id" | "center_type_id";
    msg: string;
  } | null;
  openSelectDistrict: boolean;
  openSelectCenterType: boolean;
}

export class AddCenter extends Component<AddCenterProps, AddCenterState> {
  constructor(props: AddCenterProps) {
    super(props);

    this.state = {
      loading: false,
      name: "",
      district_id: "",
      center_type_id: "",
      error: null,
      success: "",
      openSelectCenterType: false,
      openSelectDistrict: false,
    };
  }
  CreateNewTraining = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (this.state.district_id === "") {
      return this.setState({
        error: {
          target: "district_id",
          msg: "Please choose district",
        },
      });
    }
    if (this.state.center_type_id === "") {
      return this.setState({
        error: {
          target: "center_type_id",
          msg: "Please choose center type",
        },
      });
    }
    if (this.state.name === "") {
      return this.setState({
        error: {
          target: "name",
          msg: "Please fill center name",
        },
      });
    }
    this.setState({ loading: true, error: null, success: "" });
    CreateNewCenter(
      {
        name: this.state.name,
        district_id: this.state.district_id,
        center_type_id: this.state.center_type_id,
      },
      (
        loading: boolean,
        res: {
          status: "success" | "error";
          msg: string;
        } | null
      ) => {
        this.setState({ loading: loading });
        if (res?.status === "success") {
          this.setState({
            error: null,
            success: "Center added successfully!",
            loading: false,
            name: "",
          });
          this.props.onCreated();
        }
        if (res?.status === "error") {
          this.setState({
            error: {
              target: "main",
              msg: res.msg,
            },
            loading: false,
            success: "",
          });
        }
      }
    );
  };
  render() {
    return (
      <div>
        <form onSubmit={this.CreateNewTraining} className="w-full my-3">
          <div
            className="grid grid-cols-12 gap-2"
            style={
              this.state.openSelectDistrict === true ||
              this.state.openSelectCenterType === true
                ? { minHeight: "300px" }
                : {}
            }
          >
            <div className="col-span-12 md:col-span-6">
              <div>Choose district</div>
              <div
                onClick={() =>
                  this.setState({
                    openSelectDistrict: !this.state.openSelectDistrict,
                  })
                }
                className="border border-gray-400 rounded px-3 py-2 w-full flex flex-row items-center justify-between gap-2 cursor-pointer hover:border-accent-900 hover:text-accent-900"
              >
                <div className="w-52 truncate">
                  {this.state.district_id === "" ? (
                    <span className="italic text-gray-400">Not selected</span>
                  ) : (
                    `${this.state.district_id} - ${
                      this.props.districts.find(
                        (itm) =>
                          itm.district_code.toString() ===
                          this.state.district_id.toString()
                      )?.district_name
                    }`
                  )}
                </div>
                <div>
                  <IoIosArrowDown />
                </div>
              </div>
              {this.state.openSelectDistrict === true && (
                <div className="absolute mt-2 bg-white" style={{ zIndex: 9 }}>
                  <SelectCustom
                    loading={false}
                    id={"district_code"}
                    title={"district_name"}
                    close={() => this.setState({ openSelectDistrict: false })}
                    data={this.props.districts}
                    click={(response: {
                      district_code: string;
                      district_name: string;
                    }) => {
                      this.setState({
                        district_id: response.district_code,
                        openSelectDistrict: false,
                        error: null,
                      });
                    }}
                  />
                </div>
              )}
              {this.state.error?.target === "district_id" && (
                <div className="text-red-700">{this.state.error.msg}</div>
              )}
            </div>
            <div className="col-span-12 md:col-span-6">
              <div>Choose center type</div>
              <div
                onClick={() =>
                  this.setState({
                    openSelectCenterType: !this.state.openSelectCenterType,
                  })
                }
                className="border border-gray-400 rounded px-3 py-2 w-full flex flex-row items-center justify-between gap-2 cursor-pointer hover:border-accent-900 hover:text-accent-900"
              >
                <div className="w-52 truncate">
                  {this.state.center_type_id === "" ? (
                    <span className="italic text-gray-400">Not selected</span>
                  ) : (
                    `${this.state.center_type_id} - ${
                      this.props.centerTypes.center_types.find(
                        (itm) =>
                          itm.id.toString() ===
                          this.state.center_type_id.toString()
                      )?.name
                    }`
                  )}
                </div>
                <div>
                  <IoIosArrowDown />
                </div>
              </div>
              {this.state.openSelectCenterType === true && (
                <div className="absolute mt-2 bg-white" style={{ zIndex: 9 }}>
                  <SelectCustom
                    loading={false}
                    id={"id"}
                    title={"name"}
                    close={() => this.setState({ openSelectCenterType: false })}
                    data={this.props.centerTypes.center_types}
                    click={(response: { id: string; name: string }) => {
                      this.setState({
                        center_type_id: response.id,
                        openSelectCenterType: false,
                        error: null,
                      });
                    }}
                  />
                </div>
              )}
              {this.state.error?.target === "center_type_id" && (
                <div className="text-red-700">{this.state.error.msg}</div>
              )}
            </div>
            <TextInput
              onChange={(e) =>
                this.setState({
                  name: e.target.value,
                  error: null,
                  success: "",
                })
              }
              value={this.state.name}
              error={
                this.state.error !== null && this.state.error.target === "name"
                  ? this.state.error.msg
                  : ""
              }
              label={"Center name"}
              placeholder={""}
              name={""}
              MainContainer_className="col-span-12"
              disabled={this.state.loading}
            />
          </div>
          {this.state.error !== null && this.state.error.target === "main" && (
            <div
              onClick={() => this.setState({ error: null, success: "" })}
              className="cursor-pointer px-3 py-2 font-bold bg-red-100 text-red-700 flex flex-row items-center justify-between w-full my-2"
            >
              <span>{this.state.error.msg}</span>
              <div>
                <MdClose className="text-2xl" />
              </div>
            </div>
          )}
          {this.state.success !== "" && (
            <div
              onClick={() => this.setState({ error: null, success: "" })}
              className="cursor-pointer px-3 py-2 font-bold bg-green-100 text-green-700 flex flex-row items-center justify-between w-full my-2"
            >
              <span>{this.state.success}</span>
              <div>
                <MdClose className="text-2xl" />
              </div>
            </div>
          )}
          <div className="flex flex-row items-center justify-end gap-2 mt-3">
            {this.state.loading === true && (
              <div className="flex flex-row items-center gap-2">
                <div>
                  <AiOutlineLoading3Quarters
                    className={`loading-animate text-5xl text-accent-900 dark:text-white`}
                  />
                </div>
                <div className="text-lg font-bold animate__animated animate__fadeIn animate__infinite">
                  Saving, please wait...
                </div>
              </div>
            )}
            {this.state.loading === false && (
              <Button
                type="submit"
                theme="accent"
                disabled={this.state.loading}
              >
                {"Create center"}
              </Button>
            )}
          </div>
        </form>
      </div>
    );
  }
}
