import React, { Component, Fragment } from "react";
import { BiReset } from "react-icons/bi";
import { FaChalkboardTeacher } from "react-icons/fa";
import { IoIosArrowDown } from "react-icons/io";
import { MdClose, MdKeyboardArrowDown } from "react-icons/md";
import { connect } from "react-redux";
import { Auth, Basic } from "../../actions";
import { TrainingByImplementationStatusDetails } from "../../actions/training-offers";
import {
  CenterItemInterface,
  CenterTypeDetailsInterface,
  GetAllCenters,
  GetAllCentersByTrainingID,
  GetCenterTypes,
  TrainingCenterItemInterface,
} from "../../actions/training_settings_create_edit";
import { FC_GetDistricts } from "../../actions/transfer.action";
import Button from "../../components/Buttons/Button";
import Loading from "../../components/Loading/Loading";
import Modal, { ModalMarginTop, ModalSize } from "../../components/Modal/Modal";
import SelectCustom from "../../components/SelectCustom/SelectCustom";
import { StoreState } from "../../reducers";
import { DistrictTypes } from "../../types/TransferTypes";
import { search } from "../../utils/functions";
import { AddCenter } from "./AddCenter";
import { CentersList } from "./CentersList";
import { TrainingsList } from "./TrainingsList";

interface CentersManagementProps {}
interface CentersManagementState {
  loading: boolean;
  centers: CenterItemInterface[] | null;
  centerTypes: CenterTypeDetailsInterface | null;
  success: string;
  error: string;
  search_value: string;
  selected_district_code: string;
  openSelectDistrict: boolean;
  addCenter: boolean;
  districts: DistrictTypes[];
  openSelectTraining: boolean;
  selectedTraining: TrainingByImplementationStatusDetails | null;
}

class _CentersManagement extends Component<
  CentersManagementProps,
  CentersManagementState
> {
  constructor(props: CentersManagementProps) {
    super(props);

    this.state = {
      loading: true,
      centers: null,
      centerTypes: null,
      success: "",
      error: "",
      search_value: "",
      selected_district_code: "",
      openSelectDistrict: false,
      addCenter: false,
      districts: [],
      openSelectTraining: false,
      selectedTraining: null,
    };
  }
  GetAllCentersList = () => {
    this.setState({ loading: true, error: "", success: "" });
    GetAllCenters(
      (
        loading: boolean,
        res: {
          status: "success" | "error";
          msg: string;
          data: CenterItemInterface[];
        } | null
      ) => {
        this.setState({ loading: loading });
        if (res?.status === "success") {
          this.setState({
            loading: false,
            centers: res.data,
            error: "",
            success: "",
          });
        }
        if (res?.status === "error") {
          this.setState({
            loading: false,
            centers: [],
            error: res.msg,
            success: "",
          });
        }
      }
    );
  };
  GetAllCentersListByTrainingSelected = (training_id: string) => {
    this.setState({ loading: true, error: "", success: "" });
    GetAllCentersByTrainingID(
      training_id,
      (
        loading: boolean,
        res: {
          status: "success" | "error";
          msg: string;
          data: TrainingCenterItemInterface[];
        } | null
      ) => {
        this.setState({ loading: loading });
        if (res?.status === "success") {
          this.setState({
            loading: false,
            centers: res.data.map((item) => ({
              id: item.center_id,
              center_name: item.name,
              center_type_id: "",
              center_type_name: "Unknown",
              district_id: "",
              district_name: item.district_name,
              status: "",
            })),
            error: "",
            success: "",
          });
        }
        if (res?.status === "error") {
          this.setState({
            loading: false,
            centers: [],
            error: res.msg,
            success: "",
          });
        }
      }
    );
  };
  GetAllCenterTypesDetails = () => {
    this.setState({ loading: true, error: "", success: "" });
    GetCenterTypes(
      (
        loading: boolean,
        res: {
          status: "success" | "error";
          msg: string;
          data: CenterTypeDetailsInterface;
        } | null
      ) => {
        this.setState({ loading: loading });
        if (res?.status === "success") {
          this.setState({
            error: "",
            success: "",
            centerTypes: res.data,
            loading: false,
          });
        }
        if (res?.status === "error") {
          this.setState({
            loading: false,
            centerTypes: {
              center_types: [],
              center_room_type: [],
            },
            error: res.msg,
            success: "",
          });
        }
      }
    );
  };
  LoadAllDistricts = () => {
    this.setState({ loading: true });
    FC_GetDistricts(
      (status: boolean, data: DistrictTypes[] | null, msg: string) => {
        if (data !== null) {
          this.setState({ districts: data, loading: false });
        }
      }
    );
  };
  GetFilteredData = () => {
    if (this.state.centers === null) {
      return [];
    }
    var response = this.state.centers;
    if (this.state.selected_district_code !== "") {
      response = response.filter(
        (itm) =>
          itm.district_name.toString() ===
          this.state.selected_district_code.toString()
      );
    }
    return search(response, this.state.search_value) as CenterItemInterface[];
  };
  componentDidMount(): void {
    this.GetAllCenterTypesDetails();
    this.GetAllCentersList();
    this.LoadAllDistricts();
  }
  render() {
    return (
      <Fragment>
        <div className="container mx-auto p-3">
          {/* Top menu bar */}
          <div className="bg-white rounded-md p-3">
            <div className="grid grid-cols-12 gap-2">
              <div className="col-span-6">
                <div className="flex flex-col pl-2">
                  <div className="text-2xl font-extrabold">
                    Training centers management
                    {/* <span className="text-sm font-bold px-2 rounded-md bg-primary-100 text-primary-800">
                  {this.state.selected_status}
                </span> */}
                  </div>
                  <div className="text-sm text-gray-500">
                    List of centers in different locations
                  </div>
                </div>
              </div>
              <div className="col-span-6 flex flex-row items-center justify-end w-full gap-2">
                <div
                  onClick={() => this.setState({ openSelectTraining: true })}
                  className="flex flex-row items-center gap-3 p-1 hover:bg-yellow-50 rounded-md cursor-pointer border border-yellow-300 group"
                >
                  <div>
                    <div className="bg-yellow-100 rounded-md h-12 w-12 flex items-center justify-center">
                      <FaChalkboardTeacher className="text-3xl text-accent-900" />
                    </div>
                  </div>
                  <div className="flex flex-col">
                    <div className="text-sm text-gray-500 group-hover:text-black">
                      Filter by training training
                    </div>
                    <div
                      className="font-bold text-base -mt-1 truncate"
                      style={{ width: "300px" }}
                    >
                      Training name Lorem ipsum dolor sit amet consectetur
                      adipisicing elit. Pariatur suscipit accusamus ratione?
                      Nisi officia molestias omnis facilis non veniam, dolores
                      reprehenderit dolor, ratione enim libero aspernatur
                      consequatur, accusamus fugiat error!
                    </div>
                  </div>
                  <div>
                    <MdKeyboardArrowDown className="text-2xl text-gray-400 group-hover:text-black" />
                  </div>
                </div>
              </div>
            </div>
          </div>
          {this.state.loading === true ||
          this.state.centerTypes === null ||
          this.state.centers === null ? (
            <div className="flex flex-col items-center justify-center bg-white rounded-md mt-2 py-5">
              <Loading />
              <div className="text-center text-xl font-light text-gray-500">
                Loading, please wait...
              </div>
            </div>
          ) : (
            <div className="bg-white rounded-md mt-2 py-2">
              <div className="flex flex-row items-center justify-between mb-3 mx-2 gap-3">
                <div className="relative w-1/3">
                  {this.state.openSelectDistrict === false ? (
                    <div
                      onClick={() =>
                        this.setState({
                          openSelectDistrict: !this.state.openSelectDistrict,
                        })
                      }
                      className="border border-gray-400 rounded px-3 py-2 w-full flex flex-row items-center justify-between gap-2 cursor-pointer hover:border-accent-900 hover:text-accent-900"
                    >
                      <div className="w-52 truncate">
                        {this.state.selected_district_code === ""
                          ? "Filter by district"
                          : `${this.state.selected_district_code} - ${
                              this.state.districts.find(
                                (itm) =>
                                  itm.district_name.toString() ===
                                  this.state.selected_district_code.toString()
                              )?.district_name
                            }`}
                      </div>
                      <div>
                        <IoIosArrowDown />
                      </div>
                    </div>
                  ) : (
                    <div
                      onClick={() =>
                        this.setState({ openSelectDistrict: false })
                      }
                      className="font-bold flex flex-row items-center gap-4 cursor-pointer hover:text-accent-900"
                    >
                      <span>Select district</span>
                      <div>
                        {this.state.selected_district_code !== "" ? (
                          <div
                            onClick={() =>
                              this.setState({
                                selected_district_code: "",
                                openSelectDistrict: false,
                              })
                            }
                            className="font-normal text-sm cursor-pointer px-2 py-1 rounded-md bg-accent-400 text-accent-900 flex flex-row items-center justify-center gap-2"
                          >
                            <div>
                              <BiReset className="text-xl" />
                            </div>
                            <span>Reset</span>
                          </div>
                        ) : (
                          <MdClose />
                        )}
                      </div>
                    </div>
                  )}
                  {this.state.openSelectDistrict === true && (
                    <div className="w-full absolute mt-2 bg-white">
                      <SelectCustom
                        loading={false}
                        id={"district_code"}
                        title={"district_name"}
                        close={() =>
                          this.setState({ openSelectDistrict: false })
                        }
                        data={this.state.districts}
                        click={(response: {
                          district_code: string;
                          district_name: string;
                        }) => {
                          this.setState({
                            selected_district_code: response.district_code,
                            openSelectDistrict: false,
                          });
                        }}
                      />
                    </div>
                  )}
                </div>
                {/* ------------------------------- */}

                <input
                  type="search"
                  className="px-3 py-2 rounded w-1/2 border border-gray-400"
                  placeholder="Search..."
                  value={this.state.search_value}
                  onChange={(e) =>
                    this.setState({ search_value: e.target.value })
                  }
                />
                <Button onClick={() => this.setState({ addCenter: true })}>
                  Add center
                </Button>
              </div>
              {/* Centers list */}
              <CentersList
                GetFilteredData={this.GetFilteredData}
                centers={this.state.centers}
                districts={this.state.districts}
              />
            </div>
          )}
        </div>
        {this.state.addCenter === true && (
          <Modal
            backDrop={true}
            close={() => this.setState({ addCenter: false })}
            backDropClose={false}
            displayClose={true}
            padding={{
              title: true,
              body: true,
              footer: undefined,
            }}
            title="Add training center"
            widthSizeClass={ModalSize.large}
          >
            {this.state.centerTypes !== null && (
              <AddCenter
                districts={this.state.districts}
                centerTypes={this.state.centerTypes}
                onCreated={() => this.GetAllCentersList()}
              />
            )}
          </Modal>
        )}
        {this.state.openSelectTraining === true && (
          <Modal
            backDrop={true}
            close={() => this.setState({ openSelectTraining: false })}
            backDropClose={true}
            displayClose={true}
            padding={{
              title: true,
              body: true,
              footer: undefined,
            }}
            title="Available trainings"
            widthSizeClass={ModalSize.maxWidth}
            marginTop={ModalMarginTop.small}
          >
            <div className="-mt-5">
              {this.state.centerTypes !== null && (
                <TrainingsList
                  onSelected={(data: TrainingByImplementationStatusDetails) =>
                    this.GetAllCentersListByTrainingSelected(data.id)
                  }
                />
              )}
            </div>
          </Modal>
        )}
      </Fragment>
    );
  }
}
const mapStateToProps = ({
  auth,
  basic,
}: StoreState): { auth: Auth; basic: Basic } => {
  return { auth: auth, basic: basic };
};

export const CentersManagement = connect(
  mapStateToProps,
  {}
)(_CentersManagement);
