export const GetParentsToAttendTraining = async (
  data: {
    topic_id: string;
    sub_area_id: number;
    nid: string;
    start_date: string;
    end_date: string;
  },
  setResponse: (
    loading: boolean,
    res: {
      type: "success" | "error";
      msg: string;
      new: boolean;
      data: SearchParentResponseInterface | null;
    } | null
  ) => void
) => {
  setResponse(true, null);
  try {
    const res = await axios.post<SearchParentResponseInterface>(
      `${TRAINING_APP_URL}/training/search/parent`,
      data
    );
    console.log("Parents/Other users to be trained: ", res.data);
    if (res) {
      setResponse(false, {
        data: res.data,
        type: "success",
        new: false,
        msg: "Data loaded successfully",
      });
    }
  } catch (error: any) {
    setResponse(false, {
      data: null,
      type: "error",
      new:
        error?.response?.status !== undefined && error?.response?.status === 400
          ? true
          : false,
      msg: returnErrorToText(error),
    });
    console.log("Err: ", { ...error });
  }
};

