// Get District training added participants
export const GetDistrictTrainingParticipants = async (
  training_id: string,
  district_code: string,
  setResponse: (
    loading: boolean,
    res: {
      type: "success" | "error";
      msg: string;
      data: {
        school_parents: [];
        school_teachers: DistrictTrainingParticipants[];
      } | null;
    } | null
  ) => void
) => {
  setResponse(true, null);
  try {
    const res = await axios.get<{
      school_parents: [];
      school_teachers: DistrictTrainingParticipants[];
    }>(
      `${TRAINING_APP_URL}/training/trainee/district/${training_id}/${district_code}`
    );
    console.log("Res participants: ", res.data);
    if (res) {
      const response: DistrictTrainingParticipants[] = [];
      for (const item of res.data.school_teachers) {
        if (
          response.find((itm) => itm.user_id === item.user_id) === undefined
        ) {
          response.push({
            email: item.email,
            first_name: item.first_name,
            last_name: item.last_name,
            nid: item.nid,
            phone_numbers: item.phone_numbers,
            user_id: item.user_id,
            role: item.role,
            role_id: item.role_id,
            staff_code: item.staff_code,
          });
        }
      }
      setResponse(false, {
        data: {
          school_parents: [],
          school_teachers: response,
        },
        type: "success",
        msg: "Data loaded successfully",
      });
    }
  } catch (error: any) {
    setResponse(false, {
      data: null,
      type: "error",
      msg: returnErrorToText(error),
    });
    console.log("Err: ", { ...error });
  }
};

